<template>
	<div class="container">
		<div class="container-bj">
          <img src="../../assets/zhangjiaoshou.jpg">
          <span>张较瘦</span>
          <p>路漫漫其修道远，吾将上下而求索</p>
      	</div>
		<div class="container-integral">
          <p>
              <span>0</span>
              <span>优惠券</span>
          </p>
          <p>
              <span>0</span>
              <span>换鼓励金</span>
          </p>
          <p>
              <span>0</span>
              <span>积分</span>
          </p>
      	</div>
		<router-link class="container-order" to="/order" tag="div">
         <div class="container-order-1">
             <p class="left">我的订单</p>
             <p class="right">全部订单 ></p>
         </div>
         <div class="container-order-2">
              <p v-for = "list in container" :key="list.id">
                 <img :src="list.img">
                 <span>{{list.name}}</span>
              </p>
         </div>
     </router-link>
	 <div class="container-con" id="transition">
          <router-link to="/MyCollection" class="con">
                 <div class="con-left">
                  <i class="iconfont icon-collection"></i>
                  <span>我的收藏</span>
              </div>
              <div class="con-rigth">
                  <i class="fa fa-angle-right"></i>
              </div>
          </router-link>

            <router-link to="/address" class="con" id="transition">
                 <div class="con-left">
                  <i class="iconfont icon-gouwuche"></i>
                  <span>我的收货地址</span>
              </div>
              <div class="con-rigth">
                  <i class="fa fa-angle-right"></i>
              </div>
          </router-link>

           <router-link to="/cart" class="con" id="transition">
                 <div class="con-left">
                  <i class="iconfont icon-gouwuche"></i>
                  <span>我的购物车</span>
              </div>
              <div class="con-rigth">
                  <i class="fa fa-angle-right"></i>
              </div>
          </router-link>

          <router-link to="/order" class="con">
                 <div class="con-left">
                  <i class="iconfont icon-share_icon"></i>
                  <span>扫码分享</span>
              </div>
              <div class="con-rigth">
                  <i class="fa fa-angle-right"></i>
              </div>
          </router-link>
          <router-link to="/about" class="con">
                 <div class="con-left">
                  <i class="iconfont icon-bangzhuguanyuwomen"></i>
                  <span>关于我</span>
              </div>
              <div class="con-rigth">
                  <i class="fa fa-angle-right"></i>
              </div>
          </router-link>
      </div>
	</div>
</template>

<script>
export default {
  name: "MyContainer",
  data() {
    return {
      container: [
        {
          img: "/static/img/111.png",
          name: "待付款"
        },
        {
          img: "/static/img/222.png",
          name: "待收货"
        },
        {
          img: "/static/img/333.png",
          name: "待评价"
        },
        {
          img: "/static/img/444.png",
          name: "退货/退款"
        }
      ]
    };
  },
  methods: {
    back() {
        this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  width: 100%;
  height: auto;
  .container-bj {
    width: 100%;
    height: 3.5rem;
    background: url("/static/img/bj.png") no-repeat;
    background-size: auto auto;
    background-size: 100% 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    color: #fff;
    font-size: 0.4rem;
    img {
      width: 1.5rem;
      height: 1.5rem;
      display: inline-block;
      border-radius: 50%;
      overflow: hidden;
      vertical-align: middle;
    }
    span {
      display: block;
    }
    p {
      margin: 0.1rem 0;
    }
  }
  .container-integral {
    width: 100%;
    height: 1.5rem;
    background: #ffffff;
    display: flex;
    justify-content: center;
    p {
      width: 33%;
      height: 100%;
      font-size: 0.3rem;
      line-height: 0.4rem;
      font-weight: 500;
      float: left;
      display: flex;
      flex-direction: column;
      text-align: center;
      justify-content: center;
    }
  }
  .container-order {
    width: 100%;
    height: 3rem;
    background: white;
    display: block;
    margin-bottom: 0.15rem;
    margin-top: 0.15rem;
    font-size: 0.3rem;
    .container-order-1 {
      width: 100%;
      height: 1rem;
      .left {
        float: left;
        display: block;
        line-height: 1.2rem;
        margin-left: 0.4rem;
        font-size: 0.3rem;
      }
      .right {
        float: right;
        line-height: 1.2rem;
        margin-right: 0.4rem;
        font-size: 0.3rem;
      }
    }
    .container-order-2 {
      width: 100%;
      height: 1.5rem;
      display: flex;
      justify-content: center;
      align-items: center;

      p {
        width: 25%;
        display: flex;
        flex-direction: column;
        margin-top: 0.1rem;

        img {
          width: 0.6rem;
          height: 0.6rem;
          margin: auto;
        }
        span {
          text-align: center;
          padding-top: 0.3rem;
        }
      }
    }
  }
  .container-con {
    margin-bottom: 1.2rem;
    .con {
      width: 100%;
      height: 1.2rem;
      background: #ffffff;
      border-bottom: 1px solid #f0f0f0;
      display: block;
      .con-left {
        float: left;
        line-height: 1.2rem;
        padding-left: 0.47rem;
        i {
			display: block;
			float: left;
          font-size: 0.35rem;
        }

        span {
			float: left;
          font-size: 0.3rem;
          padding-left: 0.1rem;
        }
      }

      .con-rigth {
        float: right;
        line-height: 1.2rem;
		i {
		  font-size: 0.35rem;
		  padding-right: 0.4rem;
        }
      }
    }
  }
}
</style>


