<template>
    <div class="notFund">
        <div class="margtn"></div>
        <div class="header2">
            <i class="break fa fa-angle-left " @click="back"></i>
            <span>注册</span>
        </div>
        <div class="zhuce-box">
            <h2>注册</h2>
            <form>
                <p>账号:</p><input type="text" id="user" v-model="data.name"><span class="error" ></span>
                <p>密码:</p><input type="password" id="pass" v-model="data.pass"><span class="error" ></span>
                <p>确认密码:</p><input type="password" id="validatePass"><span class="error"></span>
                <p>手机号:</p><input type="text" id="phoneNum" ><span class="error"></span>
                <p>邮箱:</p><input type="text" id="email" > <span class="error"></span>
                <input type="submit" id="btn" value="注册" @click='!zhuce(data)?"":routers()' >
            </form>
        </div>
    </div>
</template>

<script>
import { mapActions } from "vuex";
import Vue from "vue";
export default {
  name: "zhuce",
  data() {
    return {
      data: {
        name: "",
        pass: ""
      }
    };
  },
  methods: {
    ...mapActions(["zhuce"]),
    back() {
      this.$router.go(-1)
    },
  routers() {
      console.log(5555)
    this.$router.push({
      path: "home"
    });
  }
  }
};
</script>
<style lang="scss" scoped>
.zhuce-box {
  width: 70%;
  text-align: center;
  padding: 0 15%;
  overflow: hidden;
  font-size: 0.4rem;
  p {
    display: block;
    width: 100%;
    text-align: left;
    margin: 0.1rem;
  }
  input {
    width: 98%;
    display: block;
    font-size: 0.4rem;
    border: 0;
    border-radius: 0.1rem;
    height: 0.6rem;
    border: 2px solid rgba(0, 0, 0, 0);

    &:focus {
      border: 2px solid #4dc3fb;
      box-shadow: 0 0 5px #4dc3fb;
    }
  }
  [type="submit"] {
    background: #4dc3fb;
    color: #fff;
    margin: 0.3rem 0;
    width: 100%;
    height: 1rem;
  }
}
</style>



