<template>
    <div class="home">
      <Header></Header>
       <banner></banner>
       <div class="nav-tow">
           <ul>
               <li
                v-for =" v in navTow " :key =" v.id " 
               >
                    <i>
                        <img :src="v.imgSrc" >
                    </i>
               </li>
           </ul>
       </div>
       <HomeContainer
        :datas='datas'
       ></HomeContainer>
    <Bottom></Bottom>
    </div>
</template>
<script>
import Bottom from "../common/Bottom";
import Header from "../common/Header";
import Banner from "./Banner";
import HomeContainer from "./HomeContainer";
export default {
  name: "home",
  props:['datas'],
  data() {
    return {
      navTow: [
        {
          id: 1,
          imgSrc: "../../../static/img/xuangou.jpg"
        },
        {
          id: 2,
          imgSrc: "../../../static/img/peijian.jpg"
        },
        {
          id: 3,
          imgSrc: "../../../static/img/pingbao.jpg"
        },
        {
          id: 4,
          imgSrc: "../../../static/img/dingzhi.jpg"
        }
      ]
    };
  },
 
  components: {
    Banner,
    HomeContainer,
    Header,
    Bottom
  }
};
</script>
<style lang="scss" scoped>
.home {
  position: relative;
  z-index: 0;
  .nav-tow {
    background: #fff;
    ul {
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin: 0;
      padding: 0;
      border-bottom: 1px solid #f4f4f4;
      li {
        margin: 0.3rem 0;
        width: 20%;
        height: auto;
        &:active {
          z-index: 999;
          transform: translate(0, -3px);
          transition: all 0.1s linear;
        }
        i {
          display: block;
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
</style>




