/* eslint-disable key-spacing */
/* eslint-disable no-unused-vars */
// 1.引入路由插件
import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/home/Home'
import List from '../components/list/List'
import Cart from '../components/list/Cart'
import Detailst from '../components/list/Detailst'
import NotFund from '../components/home/NotFund'
import DengLu from '../components/home/DengLu'
import ZhuCe from '../components/home/ZhuCe'
import My from '../components/My/My'
// 2.使用路由
Vue.use(VueRouter)
// 3.创建路由表并配置在路由器中
const router = new VueRouter({
  routes: [
    // 每一个对象都是一个路由
    {
      path: '/home',
      component: Home
    },
    {
      path: '/',
      redirect:'/home'
    },
    {
      path: '/list',
      component: List
    },
    {
      path: '/detailst',
      component: Detailst
    },
    {
      path: '/my',
      component: My
    },
    {
      path: '/cart',
      component: Cart
    },
    {
      path: '/denglu',
      component: DengLu
    },
    {
      path: '/zhuce',
      component: ZhuCe
    },
    {
      path: '/**',
      component: NotFund
    }
  ]
})
// 5.导出router
// module.exports = router
export default router



