<template>
    <div class="notFund">
        <div class="margtn"></div>
        <div class="header2">
            <i class="break fa fa-angle-left " @click="back"></i>
            <span>登录</span>
        </div>
        <div class="zhuce-box">
            <h2>登录</h2>
            <form>
                <p>账号:</p><input type="text" id="user" v-model="data2.name"><span class="error" ></span>
                <p>密码:</p><input type="password" id="pass" v-model="data2.pass"><span class="error" ></span>
                <input type="submit" id="btn" value="登录" @click='denglu(data2)?routers():""'>
            </form>
        </div>
    </div>
</template>
<script>
import { mapActions } from "vuex";
import Vue from "vue";
export default {
  name: "denglu",
  data() {
    return {
      data2: {
        name: "",
        pass: ""
      }
    };
  },
  methods: {
    ...mapActions(["denglu"]),
    back() {
      this.$router.go(-1);
    },
    routers() {
      this.$router.push({
        path: "home"
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.zhuce-box {
  width: 70%;
  text-align: center;
  padding: 0 15%;
  overflow: hidden;
  font-size: 0.4rem;
  p {
    display: block;
    width: 100%;
    text-align: left;
    margin: 0.1rem;
  }
  input {
    width: 98%;
    display: block;
    font-size: 0.4rem;
    border: 0;
    border-radius: 0.1rem;
    height: 0.6rem;
    border: 2px solid rgba(0, 0, 0, 0);

    &:focus {
      border: 2px solid #4dc3fb;
      box-shadow: 0 0 5px #4dc3fb;
    }
  }
  [type="submit"] {
    background: #4dc3fb;
    color: #fff;
    margin: 0.3rem 0;

    width: 100%;
    height: 1rem;
  }
}
</style>



