<template>
    <div class="goodDetail-box">
        <div class="margtn"></div>
        <div class="header2 clear">
            <i class="break fa fa-angle-left " @click="back"></i>
            <span>商品详情</span>
        </div>
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide"
                v-for="v in goods[queryId].homeSwipe" :key="v.id"
                style="width:auto;">
                <img :src="v.swipe" style="width:70%;height:auto;margin:0 15%;">
                </div>
            </div>
            <div class="swiper-pagination" ></div>
        </div>
        <div class="goodDetailMain">
            <div class="gooDetailNumber">{{goods[queryId].number}}</div>
            <div class="goodDetailName">{{goods[queryId].homeName}}</div>
            <div style="text-align: justify;font-size: 0.28rem;">
                <span style="margin-left:-.2rem;color:#FF4B3D;">【{{goods[queryId].homeBright}}】</span>
                {{goods[queryId].homeTitle}}
            </div>
            <div class="goodDetailColor">{{goods[queryId].color}}</div>
            <div class="goodDetailPaid">￥{{goods[queryId].homePrice}}</div>
            
        </div>

        <div class="goodDetailFooter">
            <div class="left">
                <div class="cart" @click="cart">
                    <div class="cartlength" style="height:100%;"></div>
                    <img src="http://p6563v2ck.bkt.clouddn.com/%E8%B4%AD%E7%89%A9%E8%BD%A6.png" >
                    <span>购物车</span>
                </div>
                <div class="collection" >
                    <div class="collection-box" style="height:1rem">
                        <i class="iconfont icon-collection"></i>
                        <span>收藏</span>
                    </div>
                    <div class="collection-box" style="height:1rem">
                        <i class="iconfont icon-shoucangxuanzhong1" style="color:red"></i>
                        <span style="color:red">取消</span>
                    </div>
                </div>
                <div class="shop">
                    <img src="http://p6563v2ck.bkt.clouddn.com/%E5%BA%97%E9%93%BA_2.png" >
                    <span>店铺</span>
                </div>
            </div>
            <div class="rigth">
                <div class="add">
                    <a href="javascript:void(0);" @click="addCar( goods[queryId] )">加入购物车</a>
                </div>
                <div class="purchase">
                    <a href="javascript:void(0);">提交订单</a>
                </div>
            </div>
        </div>
    </div>        
</template>

<script>
import Swiper from "swiper";
import Vue from "vue";
import axios from "axios";
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  name: "detailst",
  data() {
    return {
      goods: [],
      queryId: this.$router.currentRoute.query.id - 1
    };
  },
  methods: {
    ...mapActions(["addCar", "reduceCar"]),
    back() {
      this.$router.go(-1);
    },
    cart(){
    this.$router.push({
      path: 'cart'
      
    })
  },
    getGoods() {
      axios
        .get("../../../static/ceshi.json")
        .then(res => {
          this.goods = res.data.data.home;
          Vue.nextTick(() => {
            new Swiper(".swiper-container", {
              loop: true,
              autoplay: {
                delay: 2000,
                disableOnInteraction: false
              },
              pagination: {
                el: ".swiper-pagination",
                dynamicBullets: true,
                dynamicMainBullets: 2
              }
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    jia(index) {
      this.goodDetails[index].homeValue++;
    },
    jian(index) {
      if (this.goods[index].homeValue == 1) {
        this.goods[index].homeValue = 1;
      } else {
        this.goods[index].homeValue--;
      }
    }
  },
  created() {
    this.getGoods();
  },
  computed: {
    ...mapGetters(["totalInfo"]),
    ...mapState({
      cars: state => state.buycar.cars
    })
  }
};
</script>
<style lang="scss" scoped>
.goodDetail-box {
  width: 100%;
}

.goodDetailMain {
  background: #fff;
  border-bottom: 1px solid #cecece;
  padding: 0.3rem;
  .goodDetailName {
    color: #000;
    font-weight: 400;
    font-size: 0.4rem;
    line-height: 0.7rem;
  }
  .goodDetailPaid {
    color: #f81200;
    font-size: 0.55rem;
    margin-top: 0.08rem;
  }
}
.swiper-container {
  height: 6rem;
  width: 100%;
}
.goodDetailFooter {
  position: fixed;
  width: 100%;
  bottom: 0rem;
  height: 1rem;
  background: #f6f4f7;
  border-top: 1px solid #efefef;
  z-index: 99999;

  .left {
    width: 45%;
    float: left;
    position: relative;
    font-size: 0.3rem;
    .cart {
      width: 33%;
      height: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      float: right;
      position: relative;

      img {
        width: 0.5rem;
        height: 0.55rem;
        display: block;
        margin: auto;
      }

      span {
        text-align: center;
        color: #767676;
      }
    }

    .shop {
      width: auto;
      display: flex;
      justify-content: center;
      flex-direction: column;

      img {
        width: 0.55rem;
        height: 0.5 rem;
        display: block;
        margin: auto;
      }

      span {
        display: block;
        text-align: center;
        color: #767676;
      }
    }

    .collection {
      width: 33%;
      height: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      float: right;
      border-left: 1px solid #ccc;
      border-right: 1px solid #ccc;
      color: #767676;

      .collection-box {
        text-align: center;
      }

      i {
        font-size: 0.5rem;
        display: block;
        text-align: center;
      }
      span {
        text-align: center;
      }
    }
  }
  .rigth {
    width: 55%;
    float: right;
    .add {
      a {
        display: block;
        width: 50%;
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        background: #ff9800;
        color: white;
        font-size: 0.32rem;
        float: left;
      }
    }
    .purchase {
      a {
        float: left;
        display: block;
        width: 50%;
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        color: white;
        font-size: 0.32rem;
        background: #e3211e;
      }
    }
  }
}
</style>



