<template>
<div class="nav-container" :style='{height:(isShow?"100vh":"0vh")}'>
    <transition
        enter-active-class="animated bounceInDown"
        leave-active-class="animated fadeOutLeft"
    >
        <nav v-if="isShow">
            <ul>
                <li
                    v-for = 'v in navList' :key='v.id'
                >
                    <router-link :to="{path:v.name}" tag="a">
                        <span>{{v.title}}</span>
                        <i class="fa fa-angle-double-right" aria-hidden="true"></i>
                    </router-link>
                </li>
            </ul>
        </nav>
    </transition>
    <transition
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut"
    >
        <div class="mask" @click="select" v-if="isShow" ></div>
    </transition>
</div>
</template>
<script>
export default {
  name: "Menu",
  data() {
    return {
      navList: [
        {
          id: 1,
          title: "已买到的宝贝",
          name:"/myGoods"
        },
        {
          id: 2,
          title: "商品分类",
          name:"/list"
        },
        {
          id: 3,
          title: "新闻动态",
          name:"/news"
        },
        {
          id: 4,
          title: "我的购物车",
          name:"/shopCart"
        },
        {
          id: 5,
          title: "我的订单",
          name:"/myOrder"
        },
        {
          id: 6,
          title: "个人中心",
          name:"/my"
        },
        {
          id: 7,
          title: "登录",
          name:"/denglu"
        },
        {
          id: 8,
          title: "注册",
          name:"/zhuce"
        }
      ]
    };
  },
  props:['isShow','maskClick'],
  methods:{
      select(val) {
        let data = {
          cityname: val
        };
        this.$emit('maskClick',data);
      }
    }
};
</script>
<style lang="scss" scoped>
</style>




