<template>
    <div class="main">
        <h2>爆款产品</h2>
        <div class="main-box">
            <a href="#" style="display:block"><img src="https://shopstatic.vivo.com.cn/vivoshop/commodity/20180405/20180405101609299910_original.jpg" width="100%"></a>
            <ul class="clear" >
               <li v-for="v in datas" :key="v.id" @click="open(v.id)" >
                    <div class="list">
                        <div class="image">
                        <img :src="v.ImageOne" alt="图片">
                        </div>
                        <p class="name">{{v.name}}</p>
                        <p class="nametwo">{{v.nametwo}}</p>
                        <p class="Price">￥{{v.Price}}</p> 
                    </div>
               </li>
            </ul>
        </div>
    </div>
</template>
<script>
import Vue from "vue";
import { mapActions } from "vuex";
import axios from "axios";
export default {
  name: "HomeContainer",
  data() {
    return {
      datas: []
    };
  },
   methods: {
  open(e) {
    this.$router.push({
      path: 'detailst',
      query: {
        id: e
      }
    })
  },
   getData() {
      axios
        .get("./static/ceshi.json")
        .then(res => {
          this.datas = res.data.data.phone.lower[0].lower_data;
          
        })
        .catch(err => {
          console.log(err);
        });
    }
   },
  created() {
    this.getData();
  }
  
};
</script>
<style lang="scss" scoped>
.main h2 {
  display: block;
  height: 1rem;
  background: white;
  font-size: 0.3rem;
  line-height: 1rem;
  padding-left: 0.3rem;
  margin: 0;
}

.list {
  height: 4.7rem;
  background: white;
  float: left;
  width: 100%;
  border-right: 1px solid #f4f4f4;
  border-top: 1px solid #f4f4f4;
  box-sizing: border-box;
}
.main-box ul{
    margin: 0;
    padding: 0;
    margin-bottom: 1.2rem;
}
.list span {
  display: block;
  color: red;
  padding-left: 0.5rem;
  padding-top: 0.1rem;
}


ul li {
  list-style: none;
  float: left;
  width: 50%;
}
ul li:active {
    z-index: 999;
    transform: translate(0,-3px);
    box-shadow: 0  0px 15px rgba(0, 0, 0, 0.3);
    transition: all .1s linear;
}
.Price {
  font-size: 0.33rem;
  color: red;
  margin: auto;
  text-align: center;
  font-size: 0.3rem;
}

.name {
  width: 80%;
  height: 0.5rem;
  line-height: 0.5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: auto;
  font-size: 0.29rem;
  font-weight: 800;
  text-align: center;
}

.nametwo {
  height: 0.48rem;
  margin: 0;

  font-size: 0.2rem;
  text-align: center;
}

.image {
  width: 100%;
  background: white;
  img {
    width: 2.5rem;
    height: 2.3rem;
    display: block;
    margin: auto;
    margin-top: .5rem;
    margin-bottom: .2rem;
  }
}
</style>




