/* eslint-disable space-before-blocks */
/* eslint-disable no-redeclare */
/* eslint-disable no-unused-vars */
/* action 中处理都是异步请求的方法，我们将来的数据请求和数据处理的方法全都在这了写 */
import {
  SYNC_UPDATA
} from './const'
import router from 'vue-router'
import vue from 'vue'

export default {
  initCar ({
    commit
  }) {
    //  当数据更新之后执行初始化
    let cars = getCars()
    commit(SYNC_UPDATA, cars)
  },
  addCar ({
    commit
  }, good) {
    console.log('001')
    setTimeout(() => {
      let cars = getCars()
      let isHas = cars.some(item => {
        if (item.id === good.id) {
          // 成立就代表cars里面已经有了这条数据只要在num中+1就可以了
          console.log(good.id)
          item.homeValue++
          return true
        }
      })
      if (!isHas) {
        // 如果没有这条数据就代表cars里没有这条商品数据，在cars里直接添加这条数据并且数量为1
        good.homeValue = 1
        cars.push(good)
      }
      localStorage.cars = JSON.stringify(cars)
      commit(SYNC_UPDATA, cars)
    }, 200)
  },
  reduceCar ({
    commit
  }, good) {
    setTimeout(() => {
      let cars = getCars()
      cars = cars.filter((item) => {
        if (item.id === good.id) {
          item.homeValue--
          if (item.homeValue <= 0) {
            return false
          }
          return true
        }
        return true
      })

      localStorage.cars = JSON.stringify(cars)
      commit(SYNC_UPDATA, cars)
    }, 300)
  },
  // 注册
  zhuce ({
    commit
  }, data) {
    var datas = getData()
    let isHas = datas.some(item => {
      if (item.name === data.name) {
        alert('账号重复')
        return true
      }
    })
    if (!isHas) {
      // 如果没有这条数据就代表cars里没有这条商品数据，在cars里直接添加这条数据并且数量为1
      var qqq = {
        name: data.name,
        pass: data.pass
      }
      datas.push(qqq)
      localStorage.datas = JSON.stringify(datas)
      alert('注册成功')
      return false
    }
  },
  denglu ({
    commit
  }, data) {
    var datas = getData()
    let isBas = datas.some(item => {
      if (item.name === data.name && item.pass === data.pass) {
        alert('登陆成功')
        return true
      }
    })
    if (!isBas) {
      alert('账号不存在或者密码错误')
      return false
    }
  }
}
function getCars () {
  return JSON.parse(localStorage.cars ? localStorage.cars : '[]')
}

function getData () {
  return JSON.parse(localStorage.datas ? localStorage.datas : '[]')
}



