<template>
    <div class="header">
        <header class="app-header">
            <nav>
                <div class="left">
                    <div class="iconbox" @click ='isShow = !isShow'>
                        <i class="fa fa-bars"></i>
                    </div>
                    <div class="textbox">
                        <i class="iconfont icon-VIVO"></i>
                    </div>
                </div>
                <div class="right">
                    <i class="fa fa-shopping-cart"></i>
                </div>
            </nav>
            <Menu 
                @maskClick="navClose"
                :isShow = '!isShow'
            ></Menu>
        </header>
    </div>
</template>
<script>
import Menu from "./Menu";
export default {
  name: "Header",
  data() {
    return {
      isShow: true
    };
  },
  methods: {
    navClose() {
      this.isShow = !this.isShow;
    }
  },
  components: {
    Menu
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      this.isShow = false;
      next();
    });
  }
};
</script>
<style lang="scss" scoped>
</style>



