<template>
    <div>
        <div class="margtn"></div>
        <div class="header2">
            <i class="break fa fa-angle-left " @click="back"></i>
            <span>商品分类</span>
        </div>
        <div class="classify-box">
            <div class="left">
                <ul>
                  <li
                    v-for="(list,index) in datas.left" :key="index" 
                    @click="qiehuan(index)" 
                    :class="{active:index===classifyIndex}"
                  >
                    <i>{{list.name}}</i>
                  </li>
                </ul>
            </div>
            <div class="right">
              <ul>
                  <li
                    v-for='list in datas.right[this.classifyIndex].rigth_data' :key="list.id" @click="open(list.id)" 
                  >
                      <img :src="list.img" >
                      <span>{{list.name}}</span>
                  </li>
              </ul>
            </div>
        </div>
        <Bottom></Bottom>
    </div>
</template>

<script>
import axios from "axios";
import Bottom from "../common/Bottom";
import Vue from "vue";
export default {
  name: "List",
  data() {
    return {
      datas:[],
      classifyIndex:0
    };
  },
  methods: {
    open(e) {
      this.$router.push({
        path: "detailst",
        query: {
          id: e
        }
      });
    },
    back() {
      this.$router.go(-1);
    },
    qiehuan(index) {
      this.classifyIndex = index;
    },
    getData() {
      axios
        .get("./static/ceshi.json")
        .then(res => {
          this.datas = res.data.data.classify
          console.log(this.datas)
          
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  components: {
    Bottom
  },
  created() {
    this.getData();
  }
};
</script>
<style lang="scss" scoped>
.active {
  border-left: 2px solid;
  background: #ffffff;
  color: #199cfe;
}

ul {
  margin: 0;
  padding: 0;
}
.classify-box {
  display: flex;
  overflow: hidden;
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  padding-top: 1rem;
  .left {
    width: 2rem;
    height: 100%;
    background: #f6f6f6;
    margin-bottom: 1.2rem;
    font-size: 0.3rem;

    li {
      height: 1rem;
      line-height: 1rem;
      text-align: center;
    }
  }
  .right {
    flex: 1;
    height: 100%;
    background: white;
    margin-bottom: 1.51rem;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        display: flex;
        flex-direction: column;
        text-align: center;
        width: 33%;
        margin-top: 0.3rem;
        font-size: 0.34rem;
        float: left;
        img {
          width: 1.5rem;
          display: block;
          margin: auto;
        }
        span {
          color: #999;
          line-height: 0.7rem;
          font-size: 0.25rem;
        }
      }
    }
  }
}
</style>



