<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
        <div class="swiper-slide"
          v-for="v in bannerData" :key="v.id"
        >
          <img :src="v.imgSrc">
        </div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
import Swiper from "swiper";
import Vue from "vue";
import axios from "axios";
export default {
  name: "banner",
  data() {
    return {
      bannerData: []
    };
  },
  methods: {
    getbannerData() {
      axios
        .get("./static/ceshi.json")
        .then(res => {
          this.bannerData = res.data.data.banner;
          Vue.nextTick(() => {
            new Swiper(".swiper-container", {
              loop: true,
              autoplay: {
                delay: 2000,
                disableOnInteraction: false
              },
              pagination: {
                el: ".swiper-pagination",
                dynamicBullets: true,
                dynamicMainBullets: 2
              }
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    this.getbannerData();
  }
};
</script>


<style lang="scss" scoped>

</style>


