<template>
    <div class="notFund">
        <div class="header2">
            <i class="break fa fa-angle-left " @click="back"></i>
            <span>未知页面</span>
        </div>
        <div class="margtn"></div>
        <div class="notFund-box">
            <i class="fa fa-drupal fa-5x"></i>
            <i>404</i>

            <p class="">您访问的页面不存在</p>
        <router-link to='/home' tag="a">点击返回首页</router-link>
        </div>
        
        <bottom></bottom>
    </div>
</template>

<script>
import Bottom from "../common/Bottom";
import Vue from "vue";
export default {
  name: "notFund",
  components:{
    Bottom
  },
    methods: {
		back() {
				this.$router.go(-1)
		}
	}
  };
</script>
<style lang="scss" scoped>
    .notFund{
        .notFund-box{
            position: absolute;
            top:2rem;
            font-size: .5rem;
            width: 100%;
            i{
                margin: 0 auto;
                display: block;
                width: 2rem;
                color:#25b5fe;
                 text-align: center
            }
            p{
                display: block;
                width: 100%;
                text-align: center
            }
            a{
                display: block;
               width: 100%;
               color:#25b5fe;
                text-align: center ;
            }
        }
    }
</style>



