<template lang="html">
   
    <div class="car-box">
       <div class="header2 clear">
        <i class="break fa fa-angle-left " @click="back"></i>
        <span>购物车</span>
    </div>
    <div class="margtn"></div>
    <ul>
      <li
        v-for = ' car in cars'
        :key = 'car.commodityId'
      >
        <div class="left">
          <img :src="car.homeImg" alt="">
          <h3>{{ car.homeName }}</h3>
          <h4>单价：{{ car.homePrice }}￥</h4>
        </div>
        <div class="right">
          <button
            @click = 'reduceCar(car)'
          type="button" name="button">
            -
          </button>
          {{ car.homeValue }}
          <button
            @click = 'addCar(car)'
          type="button" name="button">
            +
          </button>
        </div>
      </li>
    </ul>
    <div class="prices">
      <p>商品总数量：{{totalInfo.num}}</p>
      <p>结算总价{{totalInfo.price}}</p>
    </div>
  </div>
</template>

<script>
import Bottom from "../common/Bottom";
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  name: "Cart",
  components: {
    Bottom
  },
  created() {
    console.log(this.cars);
  },
  computed: {
    ...mapGetters(["totalInfo"]),
    ...mapState({
      cars: state => state.buycar.cars
    })
  },
  methods: {
    ...mapActions(["addCar", "reduceCar"]),
    back() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
  .car-box{
    ul{
      margin: 0 auto;
      padding: 0;
      li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin:.1rem auto;
        padding:.2rem;
        background: #fff;
        .left{
          width:60%;
          img{
            width:2rem;height:auto;
          }
        }
        .right{
          width:40%;
          button{
            width:.5rem;
            height:.5rem;
          }
        }
      }
    }
  }
</style>



