<template>
    <div class="bottom-box">
        <ul>
            
            <router-link  to="/home" tag="li">
                <div>
                <i class="fa fa-rebel " ><span>首页</span></i>
                </div>
                
            </router-link>
            <router-link to="/list" tag="li">
                <div>
                <i class="fa fa-gg" ><span>分类</span></i>
                </div>
                
            </router-link>

            <router-link to="/news" tag="li">
                <diV>
                <i class="fa fa-yelp" ><span>动态</span></i>
                </diV>
                
            </router-link>
            <router-link to="/my" tag="li">
                <div>
                <i class="fa fa-gitlab" ><span>我的</span></i>
                </div>
                
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
  name: "Bottom"
};
</script>
<style lang="scss" scoped>
.bottom-box {
  height: 1.2rem;
  background: #fff;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99999;
}
.bottom-box ul {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  .router-link-active{
      color: #25b5fe;
  }
  li {
    float: left;
    width: 25% ;
    text-align: center;
    height: 100%;
    color: #666 ;
    div{
      height: 100%;
    i {
      font-size: 0.5rem;
      margin-top: 0.2rem;
    }
      span {
        display: block;
        margin-top: 0.1rem;
        font-size: 0.3rem;
      }
    }
  }
}
</style>



